.cards-page-container {
  max-width: 1440px;
  width: 100%;
  min-height: calc(100vh - 160px);
  height: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin: 80px auto;
  padding: 0 20px;
}
.card-container {
  width: 280px;
  height: 250px;
  margin: 20px;
  perspective: 500px;
}
.card {
  position: relative;
  width: 100%;
  height: 100%;
  border: 1px solid gray;
  box-shadow: 0px 1px 6px 3px rgba(0, 0, 0, 0.5);
  transition: all .5s;
  transform-style: preserve-3d;
  border-radius: 10px;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
}
.card:hover {
  box-shadow: 0px 1px 10px 5px rgba(0, 0, 0, 0.7);
  transform: translateZ(10px);
}
.card.disabled:hover {
  box-shadow: 0px 1px 6px 3px rgba(0, 0, 0, 0.5);
  transform: none;
  cursor: default;
}

.card-front,
.card-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  overflow: hidden;
}
.card-text {
  visibility: visible;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 100%;
  border-top: 1px solid grey;
  background-color: white;
  color: rgba(0, 0, 0, 0.5);
  font-size: 28px;
  font-weight: 500;
  border-radius: 0 0 10px 10px;
  transition: all .5s;
}
@media screen and (max-width: 850px) {
  .cards-page-container {
    padding: 0 10px;
  }
  .card-container {
    margin: 10px;
  }
}