@import "../Shared/Styles/Cards.css";

.cards-page__container {
  margin: 10px auto;
  justify-content: baseline;
  align-content: baseline;
}
/* disable button default styles */
.cards-page__card-container {
  display: block;
  padding: 0;
  border: none;
  outline: none;
  background-color: transparent;
}
.cards-page__container.hidden {
  display: none;
}
.cards-page__image {
  width: 100%;
  height: calc(100% - 60px);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 10px 10px 0 0;
  transition: all .5s;
}
.cards-page__rotate-btn {
  display: block;
  position: absolute;
  z-index: 5;
  bottom: 5px;
  right: 0;
  height: 50px;
  width: 70px;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}
.cards-page__rotate-btn > svg {
  height: 50px;
  width: 50px;
}
.cards-page__rotate-btn:hover > svg > g > path {
  fill: #00ccff7a;
}

.cards-page__card {
  transition: all 0.5s;
}
.cards-page__card.flipped {
  transform: rotateY(180deg);
}
.cards-page__card.flipped > .cards-page__rotate-btn {
  display: none;
}
.cards-page__card-front,
.cards-page__card-back {
  backface-visibility: hidden;
}
.cards-page__card-back {
  transform: rotateY(180deg);
}

.cards-page__card.play-mode > .card-front > .cards-page__image {
  height: 100%;
  border-radius: 10px;
}
.cards-page__card.play-mode > .card-front > .cards-page__card-text {
  visibility: hidden;
}
.cards-page__card.play-mode > .cards-page__rotate-btn {
  display: none;
}
.cards-page__card.disabled > .card-front > .cards-page__image {
  filter: opacity(50%);
  cursor: default;
}

.cards-page__rating {
  position: relative;
  z-index: 5;
  visibility: visible;
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  height: 40px;
  width: calc(100% - 40px);
  max-width: 1250px;
  margin: 80px auto 10px;
  overflow-x: hidden;
  transition: all ease-in-out .5s;
}
.cards-page__rating.end-game {
  position: relative;
  z-index: 5;
  visibility: visible;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: auto;
  width: calc(100% - 40px);
  max-width: 1250px;
  margin: 80px auto 10px;
  overflow-x: hidden;
  transition: all ease-in-out .5s;
}
.cards-page__rating-star {
  width: 40px;
  min-width: 40px;
  height: 40px;
  margin: 0 5px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40px 40px;
}
.cards-page__repeat-btn {
  visibility: visible;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 calc(100% / 2 - 50px / 2);
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 2px solid rgb(161, 160, 160);
  opacity: 1;
  box-shadow: 0px 0px 8px 1px rgba(0, 0, 0, 0.4);
  background-color: rgb(238, 253, 30);
  outline: none;
  transition: all ease-in-out .5s;
}
.cards-page__start-game-btn {
  visibility: visible;
  margin: 0 auto;
  margin-left: calc(100% / 2 - 200px / 2);
  height: 50px;
  width: 200px;
  border-radius: 10px;
  border: 2px solid rgb(161, 160, 160);
  color: rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 500;
  opacity: 1;
  box-shadow: 0px 0px 8px 1px rgba(0, 0, 0, 0.4);
  background-color: greenyellow;
  outline: none;
  transition: all ease-in-out .5s;
}
.cards-page__repeat-btn:hover,
.cards-page__start-game-btn:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.cards-page__rating.train-mode {
  visibility: hidden;
  height: 1px;
}
.cards-page__repeat-btn.train-mode,
.cards-page__start-game-btn.train-mode {
  visibility: hidden;
  height: 0;
  opacity: 0;
}

.cards-page__end-pop-up {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1;
  visibility: visible;
  width: 100%;
  height: 100%;
  background-color: white;
  opacity: 1;
  transition: all .5s;
  transition-delay: all .5s;
}
.cards-page__end-pop-up.hidden {
  visibility: hidden;
  opacity: 0;
  transition: all .5s;
  transition-delay: all .5s;
}
.cards-page__pop-up-img {
  height: auto;
  width: auto;
  margin: 200px auto 10px;
}
.cards-page__pop-up-text {
  font-size: 40px;
  font-weight: 500;
  text-align: center;
}