.burger-btn {
  position: relative;
  z-index: 100;
  width: 36px;
  height: 22px;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  transition: all 0.3s;
}
.burger-btn > span {
  position: absolute;
  left: 0;
  top: 10px;
  width: 100%;
  height: 3px;
  content: "";
  background-color: rgb(87, 76, 76);
  transition: 0.3s ease-in-out;
}
.burger-btn::after, 
.burger-btn::before {
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  content: "";
  background-color: rgb(87, 76, 76);
  transition: 0.3s ease-in-out;
}
.burger-btn::before {
  top: 0;
}
.burger-btn::after {
  bottom: 0;
}
.burger-btn.active::before {
  top: 10px;
  transform: rotate(45deg);
}
.burger-btn.active::after {
  bottom: 10px;
  transform: rotate(-45deg);
}
.burger-btn.active > span {
  transform: scale(0);
}

.burger-btn:hover {
  transform: scale(1.1);
}
@media screen and (min-width: 1850px) {
  .burger-btn.active::before {
    background-color: #00ccff7a;
  }
  .burger-btn.active::after {
    background-color: #00ccff7a;
  }
  .burger-btn.active > span {
    background-color: #00ccff7a;
  }
}
