.switcher-container {
  width: 160px;
  height: 40px;
  top: 50%;
  left: 50%;
}
.switcher-label {
  position: relative;
  display: flex;
  max-width: 160px;
  height: 40px;
}
  
.switcher-input {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  border-radius: 10px;
  border: 2px solid rgb(161, 160, 160);
  -webkit-appearance: none;
  background: #a1ecff;
  outline: none;
  box-shadow: inset 0px 0px 4px 1px rgba(0, 0, 0, 0.08);
  transition: 0.5s;}
  
.switcher-input:checked {
  background:#ffc7a1;
}
  
.switcher-input:before {
  content: '';
  position: absolute;
  width: 30px;
  height: 30px;
  left: 3px;
  top: 3px;
  border: 2px solid rgb(189, 188, 188);
  background: white;
  border-radius: 30%;
  transition: 0.5s;
}
  
.switcher-input:checked:before {
  left:123px;
}

.switcher-text {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: inherit;
  font-size: 10px;
  text-transform: uppercase;
  background: transparent;
  transition: 0.2s ease-out;
  transition-property: opacity;
  cursor: pointer;
}
.switcher-text:before,
.switcher-text:after {
  position: absolute;
  color: rgba(0, 0, 0, 0.5);
  font-size: 24px;
  font-weight: 500;
  line-height: 1;
  transition: inherit;
}
.switcher-text:before,
.switcher-text:after  {
  position: absolute;
  opacity: 1;
  transition: all ease 0.3s;
}
.switcher-text:before {
  right: 20px;
  opacity: 1;
  content: attr(data-off);
}
.switcher-text:after {
  left: 20px;
  opacity: 0;
  content: attr(data-on);
}
.switcher-input:checked ~ .switcher-text:before {
  opacity: 0;
}
.switcher-input:checked ~ .switcher-text:after {
  opacity: 1;
}

.switcher-text:hover:before {
  transform: translateX(-20px);
}
.switcher-text:hover:after {
  transform: translateX(20px);
}