.statistics-page {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  padding: 10px;
}
.winners__table {
  position: relative;
  display: block;
  height: 100%;
  max-width: 1200px;
  width: 100%;
  height: calc(100vh - 300px);
  margin: 10px;
  margin-bottom: 50px;
  overflow: auto;
  font-size: 22px;
  color: #574c4c;
}
.winners__table > tr {
  width: 100%;
}
.winners__table > tr > td {
  height: 100%;
  border: 1px solid #574c4c;
  padding: 5px;
  text-align: center;
  background-color: #d6f7ff;
}
.winners__table > tr > td ,
.winners__table > tr > th {
  width: 100%;
}

.winners__table > tr > td:nth-child(1),
.winners__table > tr > th:nth-child(1) {
  width: 5%;
}
.winners__table > tr > td:nth-child(2),
.winners__table > tr > th:nth-child(2) {
  width: 20%;
}
.winners__table > tr > td:nth-child(3),
.winners__table > tr > th:nth-child(3) {
  width: 20%;
}
.winners__table > tr > td:nth-child(4),
.winners__table > tr > th:nth-child(4) {
  width: 20%;
}
.winners__table > tr > td:nth-child(5),
.winners__table > tr > th:nth-child(5) {
  width: 8%;
}
.winners__table > tr > td:nth-child(6),
.winners__table > tr > th:nth-child(6) {
  width: 8%;
}
.winners__table > tr > td:nth-child(7),
.winners__table > tr > th:nth-child(7) {
  width: 8%;
}
.winners__table > tr > td:nth-child(8),
.winners__table > tr > th:nth-child(8) {
  width: 8%;
}

.winners__table-header {
  position: sticky;
  z-index: 1;
  top: 0;
  min-width: 500px;
  width: 100%;
  height: 50px;
  background-color: #ffdfca;
  color: rgb(45, 51, 56);
}
.winners__table-header > th {
  height: 100%;
  border: 1px solid #574c4c;
  padding: 5px;
}

@media screen and (max-width: 500px) {
  .winners__table {
    font-size: 16px;
  }
  .winners__table > tr > td {
    padding: 5px;
  }
}