@import './BurgerBtn.css';

.app {
  position: relative;
  width: 100%;
  min-height: calc(100vh + 10px);
  height: auto;
  background-color: rgba(172, 255, 47, 0.5);
}

.header {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 80px;
  border-bottom: 3px solid;
  border-image-slice: 1;
  border-image-source: linear-gradient(to left, #c9b1b1, #a08c8c, #c9b1b1);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  max-width: 1240px;
  width: 100%;
  margin: 0 20px;
}

.footer {
  border-top: 3px solid;
  border-image-slice: 1;
  border-image-source: linear-gradient(to left, #c9b1b1, #a08c8c, #c9b1b1);
}
.footer-container {
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
}
.github {
  display: block;
  width: 120px;
  height: 80px;
  padding-left: 50px;
  background-image: url('../assets/icons/github.svg');
  background-size: 45px;
  background-repeat: no-repeat;
  background-position: left center;
  line-height: 80px;
  color: #574c4c;
  font-size: 20px;
  letter-spacing: 0;
  transition: 0.3s;
}
.github:hover {
  letter-spacing: 2px;
}
.rss {
  display: block;
  position: relative;
  font-family: 'Open Sans', sans-serif;
  width: 86px;
  height: 40px;
  background-image: url('../assets/icons/rss.svg');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: left center;
  padding-right: 140px;
}
.rss-year {
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 21px;
  letter-spacing: 0;
  color: rgb(87, 76, 76);;
  line-height: 0.9;
  font-weight: bold;
  transition: 0.3s;
}
.rss:hover .rss-year {
  right: -9px;
  letter-spacing: 3px;
}