.sidebar__nav-menu {
  z-index: 50;
  padding-top: 80px;
  position: absolute;
  left: 0;
  top: 0;
  width: 300px;
  min-height: 620px;
  height: 100vh;
  background: #a1ecff;
  transition: 0.3s ease-in-out;
}
.sidebar__nav-menu.hidden {
  left: -300px;
}
.sidebar__nav-item {
  width: 100%;
  line-height: 50px;
  font-size: 28px;
  text-align: center;
  text-decoration: none;
}
.sidebar__nav-item > a{
  display: block;
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0.5);
  font-weight: 500;
}
.sidebar__nav-item.active {
  background: #00ccff7a;
  text-decoration: underline;
}
.sidebar__nav-item:first-child {
  line-height: 60px;
  margin-bottom: 20px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.5);
}
.sidebar__nav-item:last-child {
  position: absolute;
  bottom: 0;
  line-height: 60px;
  border-top: 2px solid rgba(0, 0, 0, 0.5);
  border-bottom: 2px solid rgba(0, 0, 0, 0.5);
}
.page-cover {
  position: absolute;
  z-index: 10;
  visibility: visible;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0);
  opacity: .5;
  transition: opacity .3s;
}
.page-cover.hidden {
  visibility: hidden;
  z-index: -10;
  opacity: 0;
}

.sidebar__nav-item:hover {
  text-decoration: underline;
}
@media screen and (max-width: 850px) {
  .sidebar__nav-menu {
    width: 250px
  }
  .sidebar__nav-menu.hidden {
    left: -250px;
  }
}