@import '../Shared/Styles/Cards.css';

.main-page__card-front {
  background-color: #a1ecff;
  border-radius: 10px;
}
.main-page__image {
  height: 160px;
  width: 160px;
  margin: 15px auto 60px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border: 5px solid white;
}
.main-page__card-text {
  position: absolute;
  bottom: 0;
  border-radius: 0 0 10px 10px;
}
.main-page__card-link {
  display: block;
  position: absolute;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: transparent;
  cursor: pointer;
}
